import { Link, useLocation } from "wouter";
import { cn } from "@/lib/utils";

type SidebarProps = {
  className?: string;
};

export default function Sidebar({ className }: SidebarProps) {
  const [location] = useLocation();

  // Define navigation items
  const navItems = [
    { href: "/", label: "Dashboard", icon: "chart-line" },
    { href: "/products", label: "Products", icon: "boxes" },
    { href: "/suppliers", label: "Suppliers", icon: "truck" },
    { href: "/stock-movements", label: "Stock Movements", icon: "exchange-alt" },
    { href: "/alerts", label: "Alerts", icon: "bell" },
    { href: "/reports", label: "Reports", icon: "file-alt" },
    { href: "/settings", label: "Settings", icon: "cog" }
  ];

  return (
    <aside className={cn("bg-sidebar shadow-md w-64 flex-shrink-0 flex flex-col h-screen z-10", className)}>
      <div className="p-4 border-b border-sidebar-border">
        <div className="flex items-center">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-primary mr-2" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
            <path d="M20 7h-9"></path>
            <path d="M14 17H5"></path>
            <path d="M17 4v16"></path>
            <path d="M7 4v16"></path>
          </svg>
          <h1 className="text-xl font-bold text-sidebar-foreground">StockTracker</h1>
        </div>
      </div>
      
      <nav className="flex-1 px-2 py-4 space-y-1">
        {navItems.map((item) => {
          const isActive = location === item.href;
          return (
            <Link 
              key={item.href}
              href={item.href}
              className={cn(
                "flex items-center px-4 py-2 text-sm font-medium rounded-md",
                isActive 
                  ? "text-white bg-primary" 
                  : "text-sidebar-foreground hover:bg-sidebar-accent"
              )}
            >
              <span className="mr-3 flex-shrink-0 h-5 w-5">
                {renderIcon(item.icon)}
              </span>
              {item.label}
            </Link>
          );
        })}
      </nav>
      
      <div className="p-4 border-t border-sidebar-border">
        <div className="flex items-center">
          <div className="h-8 w-8 rounded-full bg-primary-400 flex items-center justify-center text-white font-semibold">
            JD
          </div>
          <div className="ml-3">
            <p className="text-sm font-medium text-sidebar-foreground">John Doe</p>
            <p className="text-xs font-medium text-muted-foreground">Admin</p>
          </div>
        </div>
      </div>
    </aside>
  );
}

// Function to render icons using SVG for better customization
function renderIcon(iconName: string): React.ReactNode {
  switch (iconName) {
    case "chart-line":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M3 3v18h18"></path>
          <path d="m19 9-5 5-4-4-3 3"></path>
        </svg>
      );
    case "boxes":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M3 7v10a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V7"></path>
          <rect width="10" height="6" x="7" y="2" rx="1"></rect>
        </svg>
      );
    case "truck":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M10 17h4V5H2v12h3m15-5h-3V8l3-4h4v10h-3M5 17a2 2 0 1 0 4 0a2 2 0 1 0-4 0m15 0a2 2 0 1 0 4 0a2 2 0 1 0-4 0"></path>
        </svg>
      );
    case "exchange-alt":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="m16 3 4 4-4 4"></path>
          <path d="M20 7H9a2 2 0 0 0-2 2v9"></path>
          <path d="m8 21-4-4 4-4"></path>
          <path d="M4 17h11a2 2 0 0 0 2-2V6"></path>
        </svg>
      );
    case "bell":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M12.022 20.367a2 2 0 0 1-3.386.885M12 10a2 2 0 1 0-4 0"></path>
          <path d="M7 12a5 5 0 0 1 5-5m5 5a5 5 0 0 0-5-5"></path>
          <path d="M21 12a9 9 0 1 0-18 0v3l-2 2h20l-2-2v-3M12 19h.01"></path>
        </svg>
      );
    case "file-alt":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M14.5 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V7.5L14.5 2z"></path>
          <polyline points="14 2 14 8 20 8"></polyline>
          <line x1="16" x2="8" y1="13" y2="13"></line>
          <line x1="16" x2="8" y1="17" y2="17"></line>
          <line x1="10" x2="8" y1="9" y2="9"></line>
        </svg>
      );
    case "cog":
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <path d="M12.22 2h-.44a2 2 0 0 0-2 2v.18a2 2 0 0 1-1 1.73l-.43.25a2 2 0 0 1-2 0l-.15-.08a2 2 0 0 0-2.73.73l-.22.38a2 2 0 0 0 .73 2.73l.15.1a2 2 0 0 1 1 1.72v.51a2 2 0 0 1-1 1.74l-.15.09a2 2 0 0 0-.73 2.73l.22.38a2 2 0 0 0 2.73.73l.15-.08a2 2 0 0 1 2 0l.43.25a2 2 0 0 1 1 1.73V20a2 2 0 0 0 2 2h.44a2 2 0 0 0 2-2v-.18a2 2 0 0 1 1-1.73l.43-.25a2 2 0 0 1 2 0l.15.08a2 2 0 0 0 2.73-.73l.22-.39a2 2 0 0 0-.73-2.73l-.15-.08a2 2 0 0 1-1-1.74v-.5a2 2 0 0 1 1-1.74l.15-.09a2 2 0 0 0 .73-2.73l-.22-.38a2 2 0 0 0-2.73-.73l-.15.08a2 2 0 0 1-2 0l-.43-.25a2 2 0 0 1-1-1.73V4a2 2 0 0 0-2-2z"></path>
          <circle cx="12" cy="12" r="3"></circle>
        </svg>
      );
    default:
      return (
        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
          <circle cx="12" cy="12" r="10"></circle>
        </svg>
      );
  }
}
